.main-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    padding: 20px 2px 5px 20px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 0;
  }
}

.main-menu__item {
  text-align: center;
  border-bottom: 1px solid $border-grey;
  padding: 23px 0 19px 0;

  @media (min-width: $tablet-width) {
    margin-left: 35px;
    border-bottom: none;
  }
}

.main-menu__link {
  text-decoration: none;
  color: $text-black;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;

  @media (min-width: $tablet-width) {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    color: $text-white;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
}

.main-menu__link:hover {
  color: $hover-link-black;
  border-bottom: 2px solid $hover-link-black;
  padding-bottom: 7px;

  &:active {
    color: $active-link-black;
  }
}

.main-menu__link--active {
  @media (min-width: $tablet-width) {
    padding-bottom: 4px;
    border-bottom: 2px solid $basic-green;
  }
}

.main-menu__link--nobackground {
  @media (min-width: $desktop-width) {
    color: $text-black;
  }
}

.main-menu--closed {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}
